<template>
  <div>
    <Echart
      id="centreLeft2Chart"
      ref="centreLeft2ChartRef"
      :options="options"
      height="850px"
      width="1850px"
    ></Echart>
  </div>
</template>

<script>
import Echart from '@/common/echart';
export default {
  data() {
    return {
      options: {},
      resultdata0: [],
      resultdata1: [],
      resultdata2: [],
      titledata: [],
      sum0: 0,
      sum1: 0,
      sum2: 0,
    };
  },
  components: {
    Echart,
  },
  props: {
    cdata: {
      type: Array,
      default: () => [],
    },
  },
  watch: {
    cdata: {
      handler(newData) {
        for (var i = 0; i < newData.length; i++) {
            var d0 = {
                name: newData[i].name,
                value: newData[i].value1 + newData[i].value2
            };
            var d1 = {
                name: newData[i].name,
                value: newData[i].value1
            };
            var d2 = {
                name: newData[i].name,
                value: newData[i].value2
            };
            this.titledata.push(newData[i].name)
            this.resultdata0.push(d0);
            this.resultdata1.push(d1);
            this.resultdata2.push(d2);
            this.sum0 += newData[i].value1 + newData[i].value2;
            this.sum1 += newData[i].value1;
            this.sum2 += newData[i].value2;
        }
        this.resultdata0.sort(this.NumDescSort);
        this.resultdata1.sort(this.NumDescSort);
        this.resultdata2.sort(this.NumDescSort);
        this.options = {
            title: [{
                text: '订单始发地、目的地统计',
                // subtext: '',
                left: 'center',
                textStyle: {
                    color: '#ccc',
                    fontSize: 20
                }
            }, {
                text: '全部: ' + this.sum0,
                right: 500,
                top: 40,
                width: 100,
                textStyle: {
                    color: '#fff',
                    fontSize: 16
                }
            }, {
                text: "始发地数量: " + this.sum1,
                right: 300,
                top: 40,
                width: 100,
                textStyle: {
                    color: '#fff',
                    fontSize: 16
                }
            }, {
                text: "目的地数量: " + this.sum2,
                right: 100,
                top: 40,
                width: 100,
                textStyle: {
                    color: '#fff',
                    fontSize: 16
                }
            }, ],
            tooltip: {
                trigger: 'item'
            },
            legend: {
                orient: 'vertical',
                left: '50',
                top: '60',
                data: ['全部', '始发地数量', '目的地数量'],
                selectedMode: 'single',
                textStyle: {
                    color: '#fff',
                    fontSize: 16
                }
            },
            visualMap: {
                min: 0,
                max: 2500,
                left: '57%',
                // top: 'bottom',
                top: '600',
                text: ['高', '低'],
                calculable: true,
                colorLightness: [0.2, 100],
                color: ['#c05050', '#e5cf0d', '#5ab1ef'],
                dimension: 0,
                textStyle: {
                    color: '#ccc',
                    fontSize: 16
                }
            },
            toolbox: {
                show: false,
                orient: 'vertical',
                left: 'right',
                top: 'center',
                feature: {
                    dataView: {
                        readOnly: false
                    },
                    restore: {},
                    saveAsImage: {}
                }
            },
            grid: {
                right: 40,
                top: 100,
                bottom: '0%',
                width: '30%'
            },
            xAxis: [{
                position: 'top',
                type: 'value',
                boundaryGap: false,
                splitLine: {
                    show: false
                },
                axisLine: {
                    show: false
                },
                axisTick: {
                    show: false
                },
            }],
            yAxis: [{
                type: 'category',
                data: this.titledata,
                axisTick: {
                    alignWithLabel: true
                },
            }],
            series: [{
                z: 1,
                name: '全部',
                type: 'map',
                map: 'china',
                left: '10',
                right: '35%',
                top: 0,
                bottom: "0%",
                zoom: 0.75,
                label: {
                    normal: {
                        show: true
                    },
                    emphasis: {
                        show: true
                    }
                },
                //roam: true,
                data: this.resultdata0
            }, {
                z: 1,
                name: '始发地数量',
                type: 'map',
                map: 'china',
                left: '10',
                right: '35%',
                top: 100,
                bottom: "0%",
                zoom: 0.75,
                label: {
                    normal: {
                        show: true
                    },
                    emphasis: {
                        show: true
                    }
                },
                //roam: true,
                data: this.resultdata1
            }, {
                z: 1,
                name: '目的地数量',
                type: 'map',
                map: 'china',
                left: '10',
                right: '35%',
                top: 100,
                bottom: "0%",
                zoom: 0.85,
                label: {
                    normal: {
                        show: true
                    },
                    emphasis: {
                        show: true
                    }
                },
                //roam: true,
                data: this.resultdata2
            }, {
                name: '全部',
                z: 2,
                type: 'bar',
                label: {
                    normal: {
                        show: true
                    },
                    emphasis: {
                        show: true,
                    }
                },
                itemStyle: {
                    emphasis: {
                        color: "rgb(254,153,78)"
                    }
                },
                data: this.resultdata0
            }, {
                name: '始发地数量',
                z: 2,
                type: 'bar',
                label: {
                    normal: {
                        show: true
                    },
                    emphasis: {
                        show: true
                    }
                },
                itemStyle: {
                    emphasis: {
                        color: "rgb(254,153,78)"
                    }
                },
                data: this.resultdata1
            }, {
                name: '目的地数量',
                z: 2,
                type: 'bar',
                label: {
                    normal: {
                        show: true
                    },
                    emphasis: {
                        show: true
                    }
                },
                itemStyle: {
                    emphasis: {
                        color: "rgb(254,153,78)"
                    }
                },
                data: this.resultdata2
            }, {
                name: '全部',
                z: 2,
                type: 'pie',
                radius: ['17%', '25%'],
                center: ['10%', '82.5%'],
                label: {
                    normal: {
                        formatter: '{b}({d}%)',
                        show: true
                    },
                    emphasis: {
                        show: true,
                    }
                },
                itemStyle: {
                    emphasis: {
                        color: "rgb(254,153,78)"
                    }
                },
                data: this.resultdata0
            }, {
                name: '始发地数量',
                z: 2,
                type: 'pie',
                radius: ['17%', '25%'],
                center: ['10%', '82.5%'],
                label: {
                    normal: {
                        show: true
                    },
                    emphasis: {
                        show: true
                    }
                },
                itemStyle: {
                    emphasis: {
                        color: "rgb(254,153,78)"
                    }
                },
                data: this.resultdata1
            }, {
                name: '目的地数量',
                z: 2,
                type: 'pie',
