                    }
                },
                itemStyle: {
                    emphasis: {
                        color: "rgb(254,153,78)"
                    }
                },
                data: this.resultdata2
            }]
          };
      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
    NumDescSort(a, b) {
        return a.value - b.value;
    }
  },
